.card {
    border: 1px solid lightgrey;
    height: 250px;
    max-width: 540px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    margin: 0 auto;
    margin-top: 30px;
} 
 
.myRow {
  height: 100%;
}

.myRow > div {
  height: 100%;
}  

.galleryImage {
  max-height: 100%;
  max-width: 100%;
  width: 100%;
  height: 100%;
}

.gallery-card-buttons {
  display: flex;
  justify-content: space-evenly;
}

@media only screen and (max-width: 1000px){ 
  #mytext {
      font-size: 10px;
  }
  
}
